import { environment } from '@ali/4ever-cangjie';

function hasVertAlign(value, type) {
  return value.activeMarks
    .some((m) => m.type === 'vertAlign' && m.data.get('value') === type);
}

export function hasSubMark(value) {
  return hasVertAlign(value, 'subscript');
}

export function hasSupMark(value) {
  return hasVertAlign(value, 'superscript');
}

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';
const supKey = environment.IS_WINDOWS ? "'" : '.';
const subKey = environment.IS_WINDOWS ? ';' : ',';

export const SHORTCUT = {
  sub: `${MOD}+${subKey}`,
  sup: `${MOD}+${supKey}`,
};

export function getHotKeyTips(type: string, location: Record<string, any>) {
  const key = type.toLowerCase().concat('ButtonTitle');
  return `${location[key]} ${SHORTCUT[type]}`;
}
